import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
 
export default new Vuex.Store({
  state: {
    sernum: '',
    message: '',
    snackBar: false
  },
  mutations: {
    setSernum (state, sernum) {
      state.sernum = sernum
    },
    showSnackBar (state) {
      state.snackBar = true

    },
    setMessage (state, message) {
      state.message = message   
    }
  }
})

