
export default [
  {
    path: '/kunlun',
    meta: {
      public: true,
    },
    name: 'StationPage',
    component: () => import(
      `@/components/kunlun/StationPage/Station.vue`
    )
  },
  {
    path: '/kunlun/:station',
    meta: {
      public: true,
    },
    name: 'ContainerPage',
    component: () => import(
      `@/components/kunlun/ContainerPage/Container.vue`
    )
  },
  {
    path: '/kunlun/:station/:container',
    meta: {
      public: true,
    },
    name: 'ConnectionPage',
    component: () => import(
      `@/components/kunlun/ConnectionPage/Connection.vue`
    )
  },
  {
    path: '/set-configuration',
    meta: {
      public: true,
    },
    name: 'ConfigPage',
    component: () => import(
      `@/components/kunlun/ConfigPage/Config.vue`
    )
  },
  {
    path: '/test-data',
    meta: {
      public: true,
    },
    name: 'TestDataPage',
    component: () => import(
      `@/components/kunlun/DataPage/TestData.vue`
    )
  },
  {
    path: '/set-container/:station',
    meta: {
      public: true,
    },
    name: 'SetContainerPage',
    component: () => import(
      `@/components/kunlun/SetContainerPage/SetContainer.vue`
    )
  }
]