<template>
  <v-footer padless>
    <v-col
      class="text-center"
      cols="12"
    >
      自动化测试平台 — <strong>Kunlun @{{ new Date().getFullYear() }}</strong>
    </v-col>
    <v-snackbar
      v-model="message"
      :timeout="timeout"
    >
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color="blue"
          text
          v-bind="attrs"
          @click="store.commit('setMessage', '')"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-footer>
</template>

<script>
import store from './kunlun/store'

export default {
  name: 'Footer',
  components: {
  },
  computed: {
    message () {
      return store.state.message
    }
  },
  data: () => ({
    timeout: 3000,
  })
}
</script>
<style scoped>

</style>